/**
 * External dependencies
 */

import { search as fastSearch } from 'fast-fuzzy';

/**
 * WordPress dependencies
 */
import { useState, createContext, useContext } from '@wordpress/element';

/**
 * Module Constants
 */
const MAX_RESULTS = 24;

const StateContext = createContext();

const getType = ( obj ) => {
	if ( obj.$$typeof ) {
		return 'component';
	}

	return 'function';
};

const flatten = ( obj ) => {
	var out = [];

	for ( var key in obj ) {
		for ( var prop in obj[ key ] ) {
			if ( obj[ key ][ prop ]) {
				out.push( {
					text: `${ key }.${ prop }`,
					type: getType( obj[ key ][ prop ] ),
				} );
			}
		}
	}
	return out;
};

export function DataTabProvider( { children } ) {
	const [ action, setAction ] = useState( 'select' );
	const [ activeStore, setActiveStore ] = useState( null );
	const [ activeFunction, setActiveFunction ] = useState( null );
	let allData = flatten( wp );

	const stores = {};

	for ( let i = 0; i < Object.keys( wp ).length; i++ ) {
		const key = Object.keys( wp )[ i ];
		if ( wp[ key ].store ) {
			stores[ key ] = wp[ key ];
		}
	}
	const storeNames = Object.keys( stores ).map(
		( i ) => stores[ i ].store.name
	);

	// Add Select Functions
	for ( let k = 0; k < storeNames.length; k++ ) {
		const store = storeNames[ k ];
		allData = allData.concat(
			flatten( {
				[ `data.select('${ store }')` ]: wp.data.select( store ),
			} )
		);
	}

	// Add Dispatch Functions
	for ( let k = 0; k < storeNames.length; k++ ) {
		const store = storeNames[ k ];
		allData = allData.concat(
			flatten( {
				[ `data.dispatch('${ store }')` ]: wp.data.dispatch( store ),
			} )
		);
	}

	const search = ( keyword ) => {
		const results = fastSearch( keyword, allData, {
			ignoreCase: true,
			keySelector: ( obj ) => obj.text,
		} );
		return results.splice( 0, MAX_RESULTS );
	};

	return (
		<StateContext.Provider
			value={ {
				action,
				setAction,
				activeStore,
				setActiveStore,
				activeFunction,
				setActiveFunction,
				stores,
				search,
			} }
		>
			{ children }
		</StateContext.Provider>
	);
}

export function useDataTab() {
	const context = useContext( StateContext );

	if ( context === undefined ) {
		throw new Error( 'useData must be used within a Provider' );
	}

	return context;
}
